import React, { useEffect } from "react";
import {
  Tabs,
  TabList,
  TabPanels,
  TabPanel,
  Tab,
  Box,
  Container,
  Text,
} from "@chakra-ui/react";
import Login from "../Components/Login";
import Signup from "../Components/Signup";
import { useHistory } from "react-router-dom";

const Homepage = () => {
  const history = useHistory();

  useEffect(() => {
    const userInfo = JSON.parse(localStorage.getItem("userInfo"));
    //    setUser(userInfo);

    if (userInfo) {
      history.push("/chats");
    }
  }, [history]);

  return (
    <Container maxW="xl" centerContent>
      <Box
        display="flex"
        justifyContent={"center"}
        p={3}
        bg={"black"}
        w={"100%"}
        m="110px 0 15px 0"
        borderRadius={"lg"}
        borderWidth={"1px"}
      >
        <Text
          justifyContent={"center"}
          textAlign={"center"}
          color={"white"}
          fontFamily={"Work Sans"}
          fontSize={"4xl"}
        >
          BadBad
        </Text>
      </Box>
      <Box
        p={4}
        bg={"black"}
        w={"100%"}
        borderRadius={"lg"}
        borderWidth={"1px"}
        color={"white"}
      >
        <Tabs variant="soft-rounded" colorScheme="blue">
          <TabList mb={"1em"}>
            <Tab color={"white"} width={"50%"}>
              Login
            </Tab>
            <Tab color={"white"} width={"50%"}>
              SignUp
            </Tab>
          </TabList>
          <TabPanels>
            <TabPanel>
              {/* <p>one!</p>*/}
              <Login />
            </TabPanel>
            <TabPanel>
              {/* <p>two!</p>
               */}
              <Signup />
            </TabPanel>
          </TabPanels>
        </Tabs>
      </Box>
    </Container>
  );
};

export default Homepage;
